import { Component, OnInit } from '@angular/core'
import { AssetStatusService, AssetStatusTypeService } from 'src/app/sdk/asset/services'
import { NgxSpinnerService } from 'ngx-spinner'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router, ActivatedRoute } from '@angular/router'
import { Status, Status_type } from 'src/app/sdk/asset/models'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    constructor(
        private assetStatusService: AssetStatusService,
        private assetStatusTypeService: AssetStatusTypeService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) {}

    id_status: string = ''

    data: Status
    listStatusType: Status_type[] = []

    ngOnInit() {
        this.id_status = this.activatedRoute.snapshot.paramMap.get('id_status')
        this.getPreeData()
        this.form()
        this.getData()
    }

    getPreeData() {
        this.assetStatusTypeService.getAssetStatusTypes().subscribe(data => {
            this.listStatusType = data.data
        })
    }

    getData() {
        this.assetStatusService.getAssetStatusesId(this.id_status).subscribe(data => {
            this.dataForm.setValue(data.data)
        })
    }

    dataForm: FormGroupTypeSafe<Status>
    form() {
        this.dataForm = this.fb.group<Status>({
            id_status_type: new FormControl('', [Validators.required]),
            status: new FormControl(''),
            status_desc: new FormControl(''),
            color: new FormControl(''),
            id_user: new FormControl(''),
            id_status: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
        })
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.assetStatusService
            .putAssetStatusesId({ id: this.id_status, body: this.data })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/asset/master/status/list'])
    }
}
