import { Pegawai_status } from 'src/app/sdk/hr/models/pegawai-_status';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiStatusService } from 'src/app/sdk/hr/services/direktori-pegawai-status.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private direktoriPegawaiStatusService: DirektoriPegawaiStatusService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router

    ) { }

    data: Pegawai_status

    ngOnInit() {
        this.form()
    }

    dataForm: FormGroupTypeSafe<Pegawai_status>
    form() {
        this.dataForm = this.fb.group<Pegawai_status>(
            {
                status: new FormControl('', [Validators.required]),
                keterangan: new FormControl(''),
                code: new FormControl('')
            }
        )
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.direktoriPegawaiStatusService.postDirektoriPegawaiStatuses(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pengaturan/status/list'])
    }

}
