import { Pegawai_status } from 'src/app/sdk/hr/models/pegawai-_status';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiStatusService } from 'src/app/sdk/hr/services/direktori-pegawai-status.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { Router, ActivatedRoute } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriPegawaiStatusService: DirektoriPegawaiStatusService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private router: Router,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute
    ) { }

    data: Pegawai_status

    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id');
        this.form();
        this.getData()
    }

    id: string
    getData() {
        this.direktoriPegawaiStatusService.getDirektoriPegawaiStatusesId(this.id).subscribe(
            data => {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pegawai_status>;
    form() {
        this.dataForm = this.fb.group<Pegawai_status>(
            {
                id_pegawai_status: new FormControl('', [Validators.required]),
                status: new FormControl('', [Validators.required]),
                keterangan: new FormControl(''),
                code: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.direktoriPegawaiStatusService.putDirektoriPegawaiStatusesId({
            id: this.id,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pengaturan/status/list'])
    }

}
